.new__task {
  display: flex;
  align-items: center;

  &__input {
    max-width: 200px;
    align-items: center;
    background: #F2F7FD;
    border: 2px solid #7063C3;
    box-sizing: border-box;
    border-radius: 5px;

    outline: none;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    padding: 14px 10px;

    color: #7063C3;
    float: left;
  }

  &__accept {
    height: 30px;
    width: 30px;
    margin-left: 11px;
    background: #E1E7EE;
    border-radius: 5px;
    border: none;
    outline: none;
    color: #7063C3;
    cursor: pointer;
    font-size: 25px;
    transition: 0.1s ease;
  }
}


@media (max-width: 480px) {
  .new__task {
    display: flex;
    align-items: center;

    &__input {
      max-width: 100%;
      align-items: center;
      background: #F2F7FD;
      border: 2px solid #7063C3;
      box-sizing: border-box;
      border-radius: 5px;

      outline: none;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      display: flex;
      padding: 14px 10px;

      color: #7063C3;
      float: left;
    }

    &__accept {
      height: 30px;
      width: 30px;
      margin-left: 11px;
      background: #E1E7EE;
      border-radius: 5px;
      border: none;
      outline: none;
      color: #7063C3;
      cursor: pointer;
      font-size: 25px;
      transition: 0.1s ease;
    }
  }
}